import { Input } from '@chakra-ui/react';
import { useContext } from 'react';
import { FormContext } from '../context/Form';

export function FormInput({
  placeholder,
  name,
  maxW,
  p,
  fontSize,
  isCardInput = false,
  as,
  format,
  mask,
  type,
}) {
  const { errors, setErrors } = useContext(FormContext);

  const handleChange = () => {
    if (errors[name]) {
      setErrors({
        ...errors,
        [name]: undefined,
      });
    }
  };

  return (
    <>
      <Input
        as={as}
        format={format}
        mask={mask}
        placeholder={placeholder}
        type={type}
        name={name}
        focusBorderColor="fortini.base"
        borderColor={errors[name] ? 'error.base' : ''}
        fontSize={fontSize}
        size={{ base: 'md', xl: 'lg' }}
        maxW={maxW}
        onChange={handleChange}
        p={p ? p : '10px'}
        borderRadius="5px"
      />
      {errors[name] && !isCardInput && (
        <p className="error-message">{errors[name]}</p>
      )}
    </>
  );
}
