import { Image, Heading } from '@chakra-ui/react';

import sunriseImg from '../../../assets/time/sunrise.svg';
import cloudyImg from '../../../assets/time/cloudy.svg';
import sunImg from '../../../assets/time/sun.svg';

export function CardHead({ period }) {
  let image;
  let alt;
  let title;

  switch (period) {
    case 'morning':
      image = sunriseImg;
      alt = 'Nascer do sol';
      title = 'Manhã';
      break;
    case 'afternoon':
      image = sunImg;
      alt = 'Sol';
      title = 'Tarde';
      break;
    case 'evening':
      image = cloudyImg;
      alt = 'Lua com nuvens';
      title = 'Noite';
      break;
    default:
      image = '';
      alt = '';
      title = '';
      break;
  }

  return (
    <>
      <Image
        src={image}
        alt={alt}
        w={{ base: '60px', sm: '70px' }}
        m="10px 0"
      />
      <Heading as="h3" size="xl" mb="10px" fontFamily="Satisfy">
        {title}
      </Heading>
    </>
  );
}
