import { useToast } from '@chakra-ui/react';
import { addDoc, collection, serverTimestamp } from 'firebase/firestore';
import { useContext } from 'react';
import * as yup from 'yup';
import { FormContext } from '../context/Form';
import { db } from '../services/firebase';
import { isValidCnpj } from '../utils/validate';

/* REGRAS DE VALIDAÇÃO */
const schema = yup.object().shape({
  contactName: yup.string().required('Nome necessário'),
  contactNumber: yup
    .string()
    .min(13, 'Telefone inválido')
    .max(14, 'Telefone inválido')
    .required('Telefone necessário'),
  contactEmail: yup
    .string()
    .email('Email inválido')
    .required('Email necessário'),
  schoolName: yup.string().required('Nome necessário'),
  schoolDocument: yup.string().required('CNPJ necessário'),
  schoolNumber: yup
    .string()
    .min(13, 'Telefone inválido')
    .max(14, 'Telefone inválido')
    .required('Telefone necessário'),
  schoolEmail: yup
    .string()
    .email('Email inválido')
    .required('Email necessário'),
  directorName: yup.string().required('Nome necessário'),
  directorNumber: yup
    .string()
    .min(13, 'Telefone inválido')
    .max(14, 'Telefone inválido')
    .required('Telefone necessário'),
  directorEmail: yup
    .string()
    .email('Email inválido')
    .required('Email necessário'),
  viceDirectorName: yup.string().required('Nome necessário'),
  viceDirectorNumber: yup
    .string()
    .min(13, 'Telefone inválido')
    .max(14, 'Telefone inválido')
    .required('Telefone necessário'),
  viceDirectorEmail: yup
    .string()
    .email('Email inválido')
    .required('Email necessário'),
  meetingTimes: yup.string().required('Resposta necessária'),
  projectIDEB: yup.string().required('Resposta necessária'),
  projectSugestion: yup.string().required('Resposta necessária'),
  schoolEvents: yup.string().required('Resposta necessária'),
});

export function useForm(formRef) {
  const toast = useToast();
  const { setErrors } = useContext(FormContext);

  const handleSubmit = async event => {
    event.preventDefault();

    const formInputs = [...formRef.current.elements].filter(
      element =>
        element.localName === 'input' || element.localName === 'textarea',
    );
    let values = {};
    formInputs.forEach(input => {
      values[input.name] = input.value;
    });

    try {
      await schema.validate(values, {
        abortEarly: false,
      });

      //FIREBASE INTEGRATION
      try {
        await addDoc(collection(db, 'registrations'), {
          ...values,
          createdAt: serverTimestamp(),
        });
        toast({
          title: 'Sucesso',
          description: 'Cadastro concluído com sucesso.',
          position: 'top',
          status: 'success',
          duration: 3000, // 3 segundos
          onCloseComplete: () => {
            window.location.reload();
          },
        });
      } catch (e) {
        toast({
          title: 'Erro',
          description:
            'Ocorreu um erro e não foi possível concluir o cadastro.',
          position: 'top',
          status: 'error',
          duration: 3000, // 3 segundos
        });
      }

      setErrors({});
    } catch (err) {
      if (err instanceof yup.ValidationError) {
        const errorMessages = {};

        err.inner.forEach(error => {
          errorMessages[error.path] = error.message;
        });

        toast({
          title: 'Alerta',
          description: 'Campos não preenchidos ou inválidos',
          position: 'top',
          status: 'warning',
          duration: 3000, // 3 segundos
        });

        setErrors(errorMessages);

        if (
          !isValidCnpj(values.schoolDocument) &&
          !errorMessages.schoolDocument
        ) {
          setErrors(errors => ({
            ...errors,
            schoolDocument: 'CNPJ inválido',
          }));
        }
      }
    }
  };

  return { handleSubmit };
}
