export function formatTelephoneNumber(number) {
  let telephone = number.toString();
  let length = telephone.length;

  if (length < 3) {
    return '(' + telephone.substring(0, 2) + ')';
  } else if (length < 7) {
    return '(' + telephone.substring(0, 2) + ')' + telephone.substring(2, 6);
  } else if (length < 11) {
    return (
      '(' +
      telephone.substring(0, 2) +
      ')' +
      telephone.substring(2, 6) +
      '-' +
      telephone.substring(6, 10)
    );
  } else {
    return (
      '(' +
      telephone.substring(0, 2) +
      ')' +
      telephone.substring(2, 7) +
      '-' +
      telephone.substring(7, 11)
    );
  }
}
