//Validate CNPJ
export const isValidCnpj = documentNumber => {
  let cnpj = documentNumber.replace(/[^\d]+/g, '');

  if (cnpj.length !== 14) return false;

  if (/^(\d)\1+$/.test(cnpj)) return false;

  let lastDigitsPosition = cnpj.length - 2,
    twoLastDigits = cnpj.substring(lastDigitsPosition),
    secondtoLastDigit = parseInt(twoLastDigits.charAt(0)),
    lastDigit = parseInt(twoLastDigits.charAt(1)),
    calc = digitPosition => {
      let n = cnpj.substring(0, digitPosition),
        y = digitPosition - 7,
        s = 0,
        r = 0;

      for (let i = digitPosition; i >= 1; i--) {
        s += Number(n.charAt(digitPosition - i)) * y--;
        if (y < 2) y = 9;
      }

      r = 11 - (s % 11);
      return r > 9 ? 0 : r;
    };

  return (
    calc(lastDigitsPosition) === secondtoLastDigit &&
    calc(lastDigitsPosition + 1) === lastDigit
  );
};
